@import url("https://fonts.googleapis.com/css?family=Roboto&display=swap");
.projects {
  flex-grow: 1;
  background: #f7f7f7; }
  .projects .projects_title {
    display: flex;
    flex-direction: row;
    justify-content: center;
    font-size: 45px;
    margin-top: 2rem;
    color: #00046b; }
  .projects .repos {
    height: 100%;
    display: grid;
    grid-template-columns: repeat(1, auto);
    padding: 0% 2% 2% 2%; }
    @media only screen and (min-width: 550px) {
      .projects .repos {
        grid-template-columns: repeat(2, auto); } }
    @media only screen and (min-width: 1080px) {
      .projects .repos {
        grid-template-columns: repeat(3, auto); } }
    .projects .repos .repo {
      display: flex;
      flex-direction: column;
      background: #ffffff;
      max-width: 400px;
      margin: 5%;
      padding: 3%;
      border-radius: 5px;
      box-shadow: 0 5px 10px -2px rgba(116, 128, 143, 0.2); }
      .projects .repos .repo .name_link {
        text-decoration: none; }
        .projects .repos .repo .name_link .name {
          font-size: 2em;
          color: black;
          padding-left: 0.5em; }
      .projects .repos .repo .middle {
        flex-grow: 1;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        font-size: 1em; }
      .projects .repos .repo .bottom {
        display: flex;
        flex-direction: row;
        justify-content: space-between; }
      .projects .repos .repo .property {
        padding-top: 1em;
        padding-bottom: 1em; }
      .projects .repos .repo .description {
        max-width: 240px; }

.projects .repos {
  color: #0c6cce; }
  .projects .repos a:visited, .projects .repos a:link {
    color: #0c6cce; }
  .projects .repos a:hover {
    color: #cecece; }
